      h1 Salud ambiental y normativa asociada
    
    figure.mb-5(data-aos="fade-right")
      img(src='@/assets/curso/tema1/img01.png' alt="Imagen decorativa")

    p.mb-5 La #[strong tcnica de planificacin, plan y orden de viaje] es un procedimiento operativo que deben implementar el transportador y el conductor para asegurar que la movilizacin de animales en pie se realice dentro de los lmites establecidos de tiempo de conduccin, incluyendo las pausas obligatorias para inspeccin y descanso. Su propsito es #[strong garantizar el bienestar animal y la seguridad vial], evitando situaciones de estrs, agotamiento, lesiones o riesgos durante el desplazamiento.

    .row.justify-content-center.mb-5
      .col-lg-10
        .BG01.p-4
          .row.justify-content-center.align-items-center
            .col-lg-2.col-4.px-4.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img02.svg' alt="Imagen decorativa")
            .col-lg-10
              p.mb-0 La #[strong planificacin del viaje] es un paso esencial dentro del proceso de transporte pecuario, ya que permite anticipar las condiciones del trayecto, evaluar la aptitud de los animales para la movilizacin y prever las necesidades logsticas. Esta tcnica se centra en el #[strongg control estricto del tiempo de conduccin], el #[strong cumplimiento de las pausas de descanso], y la ejecucin de inspecciones programadas para verificar el estado de los animales, del vehculo y de la carga.
    
    p.mb-5 Una adecuada planificacin incluye:

    .row.justify-content-center.mb-5
      .col-lg-3.col-md-6.mb-lg-0.mb-4(data-aos="fade-right")
        .tarjeta-numerada.color-primario.p-5.h-100
          .tarjeta-numerada__numero
            .h4 1
          p.text-center.mb-0 Verificar la #[strong documentacin sanitaria y de movilizacin] requerida por la normativa, como la Resolucin 20223040006915.
      .col-lg-3.col-md-6.mb-lg-0.mb-4(data-aos="flip-right")
        .tarjeta-numerada.color-secundario.p-5.h-100
          .tarjeta-numerada__numero
            .h4 2
          p.text-center.mb-0 Confirmar la #[strong aptitud de los animales], evaluando su condicin fsica y descartando aquellos que no cumplen con los criterios de bienestar.
      .col-lg-3.col-md-6.mb-lg-0.mb-4(data-aos="flip-left")
        .tarjeta-numerada.color-acento-contenido.p-5.h-100
          .tarjeta-numerada__numero
            .h4 3
          p.text-center.mb-0 Revisar el #[strong estado del vehculo], garantizando que las superficies, divisiones, ventilacin y sistemas de seguridad estn en condiciones ptimas.
      .col-lg-3.col-md-6.mb-lg-0.mb-4(data-aos="fade-left")
        .tarjeta-numerada.color-secundario.ligth.p-5.h-100
          .tarjeta-numerada__numero
            .h4 4
          p.text-center.mb-0 Establecer el #[strong orden del viaje], definiendo rutas seguras, tiempos estimados, puntos de parada y protocolos de contingencia ante emergencias.

    .BG02.mb-5(data-aos="fade-right")
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-5.col-10.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img03.png' alt="Imagen decorativa")
        .col-lg-7.p-4
          .row.mb-4
            .col-lg-2.col-3
              img(src='@/assets/curso/tema1/img04.svg' alt="Imagen decorativa")
          p Una planificacin adecuada permite reducir el estrs, evitar sobrecargas, disminuir el riesgo de lesiones y asegurar que el transporte se desarrolle bajo estndares tcnicos y humanitarios.
          p.mb-0 Para garantizar una movilizacin segura y humanitaria, la planificacin del viaje debe contemplar aspectos operativos que regulen los #[strong tiempos de conduccin, los periodos de descanso y las pausas de inspeccin en ruta]. Estos elementos permiten prevenir el agotamiento del conductor, reducir el riesgo de incidentes y asegurar condiciones adecuadas para los animales durante el traslado. A continuacin, se sintetizan los aspectos clave de la planificacin, las tcnicas de ejecucin que deben aplicarse y el soporte documental correspondiente segn el Manual de Procedimiento.
    
    .row.justify-content-center.mb-4
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        AcordionA.color-acento-botones(tipo="b" clase-tarjeta="tarjeta BG03" data-aos="zoom-in")
          div(titulo="Planificacin del descanso")
            p #[strong Tcnica de ejecucin y orden de viaje]: el plan debe ordenar la detencin del vehculo y el descanso del conductor y tripulante por al menos ocho (8) horas, despus de un mximo de ocho (8) horas de conduccin continua.
            p.mb-0 #[strong Soporte documental (texto exacto)]: manual de procedimiento (Num. 4.i): "El conductor o tripulante deber descansar al menos ocho (8) horas, una vez haya conducido ocho (8) horas continuas ".
          div(titulo="Orden de inspeccin en ruta")
            p #[strong Tcnica de ejecucin y orden de viaje]: el plan debe incorporar interrupciones programadas para la inspeccin y el control. Se debe realizar una revisin de la unidad y los animales, por lo menos, cada cuatro (4) horas.
            p.mb-0 #[strong Soporte documental (texto exacto)]: manual de procedimiento (Num. 4.g): "El conductor o tripulante deber realizar la inspeccin de la unidad de transporte de animales y de los animales en movimiento, por lo menos cada cuatro (4) horas durante el viaje".
          div(titulo="Planificacin de paradas")
            p #[strong Tcnica de ejecucin y orden de viaje]: en casos de viajes de larga duracin, el plan de viaje debe identificar un lugar de descanso de los animales donde se puedan realizar actividades de inspeccin, alojamiento, hidratacin o alimentacin.
            p.mb-0 #[strong Soporte documental (texto exacto)]: manual de procedimiento (Num. 4.h): "En los casos de interrupciones, estas se deben realizar en un lugar de descanso de los animales para realizar actividades de inspeccin, acomodacin, hidratacin o alimentacin ".
      .col-lg-4.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema1/img05.jpg' alt="Imagen decorativa")

    separador
    #t_1_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.1 Especie animal en pie
    
    .row.justify-content-center.mb-5
