        p El manual de procedimientos rige el transporte de una amplia variedad de especies de produccin que, debido a sus caractersticas fsicas y comportamentales, requieren tcnicas de manejo diferenciadas y condiciones especficas en el diseo del vehculo.
        p.mb-0 La normativa aplica a mltiples especies, cuyas particularidades fsicas y de comportamiento determinan los requisitos estructurales del vehculo y las tcnicas de manejo adecuadas.

    .mb-4.d-flex(data-aos="fade-right")
