        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tipos de especies sometidas a regulacin
    
    p.mb-5 La normativa aplica a todas las especies destinadas al consumo humano, con mencin especfica a las siguientes:

