    p Garantizar un transporte seguro y humanitario requiere reconocer que cada especie posee condiciones fsicas y comportamentales particulares que influyen directamente en su susceptibilidad al estrs y en la forma en que deben ser contenidas durante el viaje.
    p.mb-5 A continuacin, se presenta informacin esencial para comprender cmo las caractersticas fsicas y comportamentales de cada especie determinan los requisitos de contencin y las condiciones mnimas de diseo del vehculo durante la movilizacin. Esta relacin permite garantizar un manejo seguro, reducir el estrs animal y prevenir lesiones, asegurando el cumplimiento de las buenas prcticas y de la normativa vigente.

    .BGIMG01.p-md-5.p-4.mb-5
      .tarjeta.tarjeta--blanca.p-4
        PasosB.color-acento-botones(data-aos="zoom-in")
