              p.mb-0 #[strong Requisito de contencin / diseo del vehculo]: divisiones o Barreras Fuertes para separacin de grupos y Piso Antideslizante para prevenir cadas.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img11.jpg' alt="Imagen decorativa")
          .row.justify-content-center.align-items-center
            .col-lg-6.order-lg-1.order-2
              .h4 Porcinos / ovinos / caprinos
              p #[strong Caractersticas fsicas clave]: sensibilidad al estrs trmico (porcinos), necesidad de densidad controlada (kg/m2) y riesgo de hacinamiento por densidad.
              p.mb-0 #[strong Requisito de contencin / diseo del vehculo]: ventilacin constante para mantener la temperatura entre 5 C y 30 C.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img12.jpg' alt="Imagen decorativa")
          .row.justify-content-center.align-items-center
