              p.mb-0 #[strong Requisito de contencin / diseo del vehculo]: cajas plsticas o de cartn seguras que deben ir fijadas o dispuestas de manera tal, que se eviten su desplazamiento y/o volcamiento.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
