        p El manejo etolgico exige separar adecuadamente a los animales dentro del vehculo para prevenir conflictos sociales y reducir el riesgo de lesiones.
        p La separacin obligatoria debe aplicarse rigurosamente a:
        ul.lista-ul--color
          li(style="margin-bottom: 2px")
            i.fas.fa-horse-head
            | Diferentes especies.
          li(style="margin-bottom: 2px")
            i.fas.fa-horse-head
