          p.mb-0 - #[strong Manual de Procedimiento (nm. 4.c.iii)].
    
    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tcnicas de manejo especficas (cargue y descargue)
    
    p.mb-5 El manejo debe adaptarse a la movilidad y caractersticas fsicas de cada especie.

