            p.mb-0 A continuacin, se presentan de manera organizada los principales aspectos, caractersticas y referencias normativas relacionadas con el transporte de especies de consumo humano, de manera que se puedan identificar de forma clara los criterios tcnicos y el soporte documental que los respalda.

        LineaTiempoD.color-acento-botones.especial
          div(numero="1" titulo="Tipos de especies")
            p El manual aplica al transporte de las siguientes especies de produccin: bovina, porcina, caprina, ovina, aviar, bufalina, quida y las dems especies de consumo humano.
            p RESOLUCIN 20223040006915 (Art. 2): "aplica a las personas naturales y jurdicas... de animales de las especies bovina, porcina, caprina, ovina, aviar, bufalina, quida y las dems especies de consumo humano ".
            figure
              img(src='@/assets/curso/tema1/img21.jpg' alt="Imagen decorativa")
          div(numero="2" titulo="Caractersticas fsicas (aves)")
            p La caracterstica de fragilidad y tamao de las aves recin nacidas requiere el uso de contenedores especficos.
            p Manual de procedimiento (Num. 2.2.c.ii): "en el caso de pollitos de un da de edad, deben transportarse en cajas plsticas o de cartn seguras y en estado adecuado".
            figure
              img(src='@/assets/curso/tema1/img22.jpg' alt="Imagen decorativa")
          div(numero="3" titulo="Tcnicas de manejo")
            p Las tcnicas de manejo deben ser especficas para la especie y su comportamiento, exigiendo la separacin por condiciones fsicas (cuernos, tamao) y reproductivas para evitar lesiones y hostilidad.
            p Manual de procedimiento (Num. 4.c.iii): exige separar por especie, edad, tamao, y animales con y sin cuernos, reconociendo sus diferentes necesidades de manejo.
            figure
              img(src='@/assets/curso/tema1/img23.jpg' alt="Imagen decorativa")

    separador
    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.2 Tcnicas de separacin
    
    .row.justify-content-center.mb-5
      .col-lg-5.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema1/img24.jpg' alt="Imagen decorativa")
      .col-lg-7(data-aos="fade-left")
        .cajon.color-secundario.p-4.mb-4  
          p.mb-0 La separacin de los animales dentro de la unidad de transporte constituye una tcnica de manejo obligatoria y esencial para garantizar el bienestar animal. Su finalidad es prevenir el sufrimiento, las lesiones, el maltrato y los comportamientos de agresividad o pelea entre los animales, situaciones que pueden afectar su salud y comprometer la calidad de la carga.
        p.mb-0 A continuacin, se presenta un recurso que sistematiza los principales #[strong criterios de separacin de animales durante el transporte], detallando la tcnica aplicada, su justificacin desde la etologa y el soporte documental correspondiente. Esta informacin orienta la toma de decisiones para garantizar el bienestar animal, prevenir riesgos sanitarios y reducir situaciones de estrs, lesiones o agresividad dentro de la unidad de transporte.

    .row.justify-content-center.mb-5
      .col-lg-10
        PasosA.color-acento-contenido(tipo="n")
