              p.mb-0 Separar por especie. Manual de Procedimiento (nm. 4.c.iii).
            .col-lg-5.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
              img(src='@/assets/curso/tema1/img25.png', alt="Imagen decorativa")
          .row.justify-content-center
            .col-lg-5.col-8.mb-lg-0.mb-4(data-aos="fade-right")
              img(src='@/assets/curso/tema1/img26.png', alt="Imagen decorativa")
            .col-lg-7(data-aos="fade-left")
              h4 Separacin por grupos etreos y tamao
              p Se deben separar los animales por edad y tamao para asegurar que los ejemplares ms pequeos o jvenes no sean aplastados o golpeados por animales ms grandes o con mayor fuerza.
              p.mb-0 Separar por edad y tamao. Manual de Procedimiento (nm. 4.c.iii).
          .row.justify-content-center
            .col-lg-7.order-lg-1.order-2(data-aos="fade-right")
              h4 Separacin por sexo y madurez
              p Se deben separar los machos sexualmente maduros de las hembras (y viceversa) para evitar el acoso sexual, el estrs y el riesgo de lesiones y cadas asociadas al comportamiento reproductivo.
              p.mb-0 Separar a los machos sexualmente maduros de las hembras. Manual de Procedimiento (nm. 4.c.iii).
            .col-lg-5.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
              img(src='@/assets/curso/tema1/img27.png', alt="Imagen decorativa")
          .row.justify-content-center
            .col-lg-5.col-8.mb-lg-0.mb-4(data-aos="fade-right")
              img(src='@/assets/curso/tema1/img28.png', alt="Imagen decorativa")
            .col-lg-7(data-aos="fade-left")
              h4 Separacin por condicin fsica y hostilidad
              p Es obligatorio separar a los animales con y sin cuernos ya aquellos animales que puedan ser hostiles con otros. Tambin se deben separar los que requieran atencin especial o que estn lesionados.
              p.mb-0 Separar a los animales con y sin cuernos, as como a aquellos que puedan ser hostiles con otros, requieran atencin especial o se encuentren lesionados. Manual de Procedimiento (nm. 4.c.iii).

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="fade-right")
        .cajon.color-primario.p-4
          p.mb-0 La separacin se aplica a diferentes niveles, por especie, por condicin fsica o por comportamiento, con el fin de reducir el riesgo de conflictos entre los animales. Esta tcnica es responsabilidad directa del conductor o del tripulante de la unidad de transporte.
    
    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Consecuencias de la falta de separacin en el bienestar animal
    
    p.mb-5 La no aplicacin de esta tcnica operativa conlleva a una vulneracin del bienestar animal, lo que puede generar las siguientes consecuencias:

    .row.justify-content-center.mb-4
      .col-lg-2.col-4.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema1/img29.svg' alt="Imagen decorativa")
      .col-lg-10(data-aos="fade-left")
        ol.lista-ol--cuadro.lista-ol--separador
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span 1
            p.mb-0 #[strong Lesiones y traumatismos]: ocasionados por peleas entre los animales o por el aplastamiento de ejemplares de menor tamao.
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span 2
            p.mb-0 #[strong Aumento del estrs]: producto del acoso constante, lo cual puede afectar la calidad y la inocuidad de la carne.

    separador
    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.3 Etapas fisiolgicas del animal, comportamiento y sentidos animal
    
    p.mb-5 El bienestar animal durante el transporte se fundamenta en el conocimiento de la etologa, entendida como el estudio del comportamiento animal, as como en la identificacin de las etapas fisiolgicas crticas que influyen directamente en la capacidad del animal para tolerar el estrs, el movimiento y las condiciones propias del transporte. Este conocimiento permite al personal aplicar un manejo humanitario, preventivo y basado en criterios tcnicos y cientficos.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Etapas fisiolgicas crticas (exclusin del transporte)
    
    p.mb-5 Las siguientes situaciones fisiolgicas representan un riesgo elevado de sufrimiento para el animal y constituyen causales obligatorias de exclusin del transporte, de acuerdo con la normativa vigente:

    .titulo-sexto.color-acento-contenido(data-aos="fade-right")
      h5 Tabla 1.
      i Etapas fisiolgicas crticas
    .tabla-a.color-acento-contenido.mb-5
      table(data-aos="zoom-in"  alt="Texto alternativo tabla")
        thead(style="border-color: transparent")
          tr(data-aos="fade-right")
            th(style="background-color: #F3BC5B; width: 18%;") Etapa fisiolgica
            th(style="background-color: #F3BC5B; width: 36%;") Criterio de riesgo
            th(style="background-color: #F3BC5B; width: 46%;") Consecuencia y tcnica de exclusin
        tbody
          tr(data-aos="fade-right")
            th(style="background-color: #F6F6F6;") Gestacin avanzada
            td(style="background-color: #F6F6F6;") ltimo tercio de gestacin, animal prximo al parto.
            td(style="background-color: #F6F6F6;") La tcnica es la exclusin de la hembra para prevenir el parto durante el transporte, el sufrimiento fetal y el riesgo de mortalidad de la madre en ruta.
          tr(data-aos="fade-right")
            th Postparto
            td Animal que ha parido siete (7) das antes de la fecha del embarque.
            td La tcnica es la exclusin durante el perodo crtico de recuperacin posparto.

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="fade-right")
        .cajon.color-secundario.p-4
          .row.justify-content-center
            .col-lg-3.col-6.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img30.svg' alt="Imagen decorativa")
            .col-lg-9
              .h4 Soporte documental
              p.mb-0 La exclusin es obligatoria para hembras prximas al parto (ltimo tercio de gestacin) o que hayan parido siete (7) das antes de la fecha del embarque (Manual de procedimiento, Nm. 4.ai).
    
    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Comportamiento animal (etologa aplicada al manejo)
    
    p El comportamiento de los animales de granja, basado en su condicin de presas, determina las tcnicas de manejo que deben emplearse durante el cargue, el descargue y el transporte. La comprensin de estas conductas reduce el riesgo de lesiones, estrs y prdidas productivas.
    p.mb-5 A continuacin, se presentan los principales comportamientos observables y su relacin con las tcnicas de manejo adecuadas:

    AcordionA.color-acento-botones.mb-5(tipo="a" clase-tarjeta="tarjeta BG03" data-aos="zoom-in")
      .row.justify-content-center(titulo="Mantenimiento del equilibrio")
        .col-lg-6.order-lg-1.order-2
          p #[strong Aplicacin en la tcnica de manejo]: la capacidad de mantener la posicin de equilibrio esttico es un indicador de aptitud para el transporte. El animal que no pueda mantener esta posicin sin ayuda debe ser excluido, ya que existe alto riesgo de cadas y lesiones en ruta.
          p.mb-0 #[strong Soporte documental]: criterio principal de exclusin para animales con problemas de movilidad (Manual de procedimiento, Nm. 4.ai).
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/tema1/img31.jpg', alt="Imagen decorativa")
      .row.justify-content-center(titulo="Respuesta al hostigamiento")
        .col-lg-6.order-lg-1.order-2
          p #[strong Aplicacin en la tcnica de manejo]: el miedo y la angustia se activan por el uso de fuerza, ruido o presin excesiva. El cargue y descargue deben realizarse con calma, sin gritos ni estmulos estresantes.
          p.mb-0 #[strong Soporte documental]: exigencia de cargue y descargue con calma, sin ruidos ni hostigamiento (Manual de procedimiento, Nm. 4).
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/tema1/img32.jpg', alt="Imagen decorativa")
      .row.justify-content-center(titulo="Agresividad y hostilidad")
        .col-lg-6.order-lg-1.order-2
          p #[strong Aplicacin en la tcnica de manejo]: la agresividad, especialmente en machos sexualmente maduros o animales con cuernos, se controla mediante la separacin por grupos para evitar conflictos y daos fsicos.
          p.mb-0 #[strong Soporte documental]: exigencia de separar machos sexualmente maduros de hembras y animales con y sin cuernos (Manual de procedimiento, Nm. 4.c.iii).
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/tema1/img33.jpg', alt="Imagen decorativa")

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Sentidos animales y su influencia en el estrs
    
    p Los animales perciben el entorno de manera distinta a los humanos. Sus sentidos son ms sensibles, por lo que un manejo inadecuado puede generar respuestas intensas de estrs y comprometer su bienestar.
    p.mb-5 En el siguiente recurso se relacionan los sentidos ms relevantes con su impacto fisiolgico y las tcnicas de cuidado recomendadas:

    .titulo-sexto.color-acento-contenido(data-aos="fade-right")
      h5 Tabla 2.
      i Sentidos animales y su influencia en el estrs
    .tabla-a.color-acento-contenido.mb-4
      table(data-aos="zoom-in"  alt="Texto alternativo tabla")
        thead(style="border-color: transparent")
          tr(data-aos="fade-right")
            th(style="background-color: #F3BC5B; width: 24%;") Sentido
            th(style="background-color: #F3BC5B; width: 38%;") Relevancia en el transporte
            th(style="background-color: #F3BC5B; width: 38%;") Tcnica de cuidado asociada
        tbody
          tr(data-aos="fade-right")
            th(style="background-color: #F6F6F6;") Odo (sensibilidad auditiva)
            td(style="background-color: #F6F6F6;") Los ruidos fuertes como gritos, bocinas, motores o golpes generan pnico, incrementan el nivel de cortisol y provocan fatiga.
            td(style="background-color: #F6F6F6;") Manejo silencioso, conduccin suave y evitacin de ruidos bruscos.
          tr(data-aos="fade-right")
            th Vista (percepcin de la luz)
            td La visin panormica y la sensibilidad a contrastes de luz y sombra pueden generar miedo o detenciones durante el desplazamiento.
            td Evitar sombras marcadas en rampas y pasillos, utilizar iluminacin uniforme.
          tr(data-aos="fade-right")
            th(style="background-color: #F6F6F6;") Tacto (movilidad)
            td(style="background-color: #F6F6F6;") La percepcin del suelo es determinante. Superficies resbaladizas generan inseguridad, resistencia al movimiento y riesgo de cadas.
            td(style="background-color: #F6F6F6;") Uso de pisos antideslizantes y rampas con inclinacin suave (mximo 20 grados).
          tr(data-aos="fade-right")
            th Condiciones fsicas generales
            td Lesiones, enfermedades o dolor afectan la fisiologa, el estado mental y el bienestar general del animal.
            td Exclusin obligatoria de animales enfermos o lesionados (ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie, pg. 2).

    separador
    #t_1_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.4 Tcnicas de eleccin de vehculo segn especie animal

    .BG02.mb-5(data-aos="fade-right")
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-5.col-10.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img34.png' alt="Imagen decorativa")
        .col-lg-7.p-4
          .row.mb-4
            .col-lg-2.col-3
              img(src='@/assets/curso/tema1/img35.svg' alt="Imagen decorativa")
          p.mb-0 La eleccin de la unidad de transporte es una tcnica de planificacin y manejo que se realiza antes del proceso de carga. El vehculo debe seleccionarse en funcin de la especie, el tamao, la cantidad de animales y sus necesidades especficas de bienestar animal, tales como el espacio disponible, la ventilacin y los sistemas de contencin, con el fin de mitigar el riesgo de lesiones, sufrimiento y estrs durante el transporte.

    .row.justify-content-center.mb-5
      .col-lg-1.col-2.px-0(data-aos="fade-right")
        .BG07.p-2.h-100
          img(src='@/assets/curso/tema1/img36.svg' alt="Imagen decorativa")
      .col-lg-9.col-10.px-0(data-aos="fade-left")
        .BG04.p-4.h-100
          p.mb-0 A continuacin, se presenta un recurso que describe los criterios tcnicos que deben considerarse en la eleccin del vehculo segn la especie animal y las exigencias normativas asociadas.

    .BGIMG01.p-md-5.p-4.mb-5
      .tarjeta.tarjeta--blanca.p-4
        PasosB.color-acento-botones(data-aos="zoom-in")
          .row.justify-content-center.align-items-center
            .col-lg-6.order-lg-1.order-2
              .h4 Capacidad y densidad
              p El vehculo debe ser seleccionado segn su rea til, garantizando espacio suficiente para que el animal pueda levantarse, acostarse y girar, de acuerdo con la densidad de carga establecida para cada especie.
              p.mb-0 #[strong Soporte documental]: manual de Procedimiento (Numeral 4.e). Procurar que se cuente con espacio disponible para que el animal pueda levantarse, acostarse y dar vuelta.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img37.jpg' alt="Imagen decorativa")
          .row.justify-content-center.align-items-center
            .col-lg-6.order-lg-1.order-2
              .h4 Proteccin climtica
              p Para todas las especies, el vehculo debe contar con techo o cubierta que proteja a los animales de las condiciones climticas adversas.
              p.mb-0 #[strong Soporte documental]: manual de Procedimiento (Numeral 2.2.c.i). Debern contar con techo o cubierta que proteja a los animales de las condiciones climticas adversas.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img38.jpg' alt="Imagen decorativa")
          .row.justify-content-center.align-items-center
            .col-lg-6.order-lg-1.order-2
              .h4 Contencin especfica
              p En especies pequeas, como pollitos de un da, el vehculo debe ser adecuado para asegurar las cajas plsticas o de cartn utilizadas como unidades de contencin, evitando su desplazamiento o volcamiento durante el transporte.
              p.mb-0 #[strong Soporte documental]: manual de Procedimiento (Numeral 2.2.c.v). Las cajas deben estar fijadas o dispuestas de manera tal que se evite su desplazamiento y/o volcamiento.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img39.jpg' alt="Imagen decorativa")

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10.order-lg-1.order-2(data-aos="fade-right")
        .mb-4.d-flex
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Tcnicas de eleccin segn necesidades especficas de la especie
        p La eleccin del vehculo tambin debe responder a las caractersticas particulares de ciertos grupos de animales, considerando sus requerimientos de manejo, contencin y bienestar.
        .mb-4.d-flex
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Especies que requieren contencin fuerte y separacin
        p.mb-0 Por ejemplo: bovinos y quidos. El vehculo debe ser seleccionado por su capacidad estructural para albergar animales de gran tamao y resistir su peso y movimiento. En estos casos se requiere:
      .col-lg-2.col-4.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema1/img40.png' alt="Imagen decorativa")  

    .row.justify-content-center.mb-5
      .col-lg-6.mb-4.mb-lg-0(data-aos="fade-right")
        .tarjeta-avatar-b.h-100
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/tema1/img41.svg' alt="Imagen decorativa")
          .tarjeta.BG03
            .p-4
              .h4 Divisiones internas
              p.mb-0 El vehculo debe contar con estructura que permita instalar barreras resistentes para separar grupos segn edad, tamao y presencia de cuernos.
      .col-lg-6(data-aos="fade-left")
        .tarjeta-avatar-b.h-100
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/tema1/img42.svg' alt="Imagen decorativa")
          .tarjeta.BG03
            .p-4
              .h4 Contencin general
              p.mb-0 La unidad debe ofrecer seguridad y robustez, estando diseada especficamente para el transporte de animales vivos.

    .row.justify-content-center.mb-5
      .col-lg-10
        .mb-4.d-flex(data-aos="fade-right")
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Especies que requieren control de microclima
        p.mb-5 Por ejemplo: porcinos y aves. Para estas especies, la seleccin del vehculo se centra en el control de temperatura y ventilacin:

        .tarjeta--container.row.mb-5
          .col-md.tarjeta.color-primario.p-5(data-aos="fade-right")
            .row.justify-content-center.mb-4
              .col-6
                figure
                  img(src='@/assets/curso/tema1/img43.svg', alt="Imagen decorativa")
            h4.text-center Ventilacin
            p.mb-0 El vehculo debe contar con dispositivos que garanticen ventilacin constante y mantengan una temperatura entre 5 C y 30 C.
          .col-md.tarjeta.color-secundario.p-5(data-aos="fade-left")
            .row.justify-content-center.mb-4
              .col-6
                figure
                  img(src='@/assets/curso/tema1/img44.svg', alt="Imagen decorativa")
            h4.text-center Altura (Porcinos)
            p.mb-0 En el caso de porcinos, los compartimentos con ventilacin natural deben contar con una altura libre mnima de 90 cm para permitir un flujo de aire adecuado.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Especies que requieren contencin en unidades externas
      
    p.mb-5 Por ejemplo: aves y pollitos de un da. El vehculo debe seleccionarse por su capacidad para transportar de forma segura unidades de contencin externas:

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="fade-right")
        .tarjeta-avatar-b
          .tarjeta-avatar-b__img(style="min-width: 10%")
            img(src='@/assets/curso/tema1/img45.svg' alt="Imagen decorativa")
          .tarjeta.BG03
            .p-4
              .h4 Capacidad de fijacin
              p.mb-0 El vehculo debe garantizar que las cajas plsticas o de cartn estn fijadas o dispuestas de manera que se evite su desplazamiento o volcamiento durante el trayecto.
    
    .row.justify-content-center.mb-4
      .col-lg-10(data-aos="fade-right")
        .cajon.color-secundario.p-4
          .row.justify-content-center
            .col-lg-3.col-6.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img46.svg' alt="Imagen decorativa")
            .col-lg-9
              .h4 Soporte documental
              p.mb-0 Resolucin 20223040006915 (Numeral 2.2.c.v). Las cajas deben estar fijadas o dispuestas de manera tal que se evite su desplazamiento y/o volcamiento.

    separador
    #t_1_5.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.5 Personas del proceso de transporte animal en pie: tipos, caractersticas y tipo de obligaciones
    
    p El proceso de transporte animal en pie involucra diferentes actores que cumplen funciones especficas y complementarias. Cada uno de ellos tiene responsabilidades definidas por la normativa vigente, orientadas a garantizar el bienestar animal, la bioseguridad y el cumplimiento legal durante todas las etapas del proceso.
    p.mb-5 A continuacin, se presenta un recurso que describe los principales tipos de personas involucradas, su rol dentro del proceso y las obligaciones que les corresponden.

    .titulo-sexto.color-acento-contenido(data-aos="fade-right")
      h5 Tabla 3.
      i Personas del proceso de transporte animal en pie
    .tabla-a.color-acento-contenido.mb-5
      table(data-aos="zoom-in"  alt="Texto alternativo tabla")
        thead(style="border-color: transparent")
          tr(data-aos="fade-right")
            th(style="background-color: #F3BC5B; width: 16%;") Tipo de persona
            th(style="background-color: #F3BC5B; width: 28%;") Caracterstica / rol principal
            th(style="background-color: #F3BC5B; width: 28%;") Tipo de obligaciones clave
            th(style="background-color: #F3BC5B; width: 28%;") Soporte documental (texto exacto)
        tbody
          tr(data-aos="fade-right")
            td(style="background-color: #F6F6F6;") Propietario, tenedor o poseedor de los animales (remitente).
            td(style="background-color: #F6F6F6;") Responsable legal de la carga viva. Debe asegurar que solo se movilicen animales aptos.
            td(style="background-color: #F6F6F6;") Observar y seleccionar los animales para detectar y excluir a los no aptos (enfermos, lesionados, en etapa crtica de gestacin).
            td(style="background-color: #F6F6F6;") "el propietario, tenedor o poseedor de los animales... deber observar y seleccionar los animales, para detectar animales no aptos para el transporte." Manual de procedimiento (nm. 4.a).
          tr(data-aos="fade-right")
            td Toda persona involucrada en el proceso.
            td Cualquier actor que intervenga en las diferentes etapas del manejo y cuidado, incluyendo el personal de carga y descargue.
            td Realizar y aprobar el curso de capacitacin en bienestar animal y portar el certificado que lo acredita.
            td "toda persona involucrada en el proceso... deber realizar un curso de capacitacin en bienestar animal y portar el documento que certifique." Manual de procedimiento (nm. 6).

