    p Durante el desplazamiento, la responsabilidad del bienestar animal recae directamente sobre quienes operan el vehculo y acompaan la carga viva. Su actuacin es clave para prevenir lesiones, estrs y situaciones de riesgo durante el trayecto.
    p.mb-5 El siguiente recurso detalla los roles y obligaciones de las personas responsables de la operacin en ruta.

