            h3.col.ps-3.mb-0 Manejo de personal y obligaciones de los operadores
        p El manejo de personal en el transporte de animales en pie se centra en la capacitacin obligatoria y en el cumplimiento de un conjunto de obligaciones operativas que garantizan la seguridad y el bienestar de la carga viva durante todas las fases del proceso.
        .mb-4.d-flex
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Manejo de personal: la capacitacin como obligacin primordial
        p La tcnica de manejo del personal se fundamenta en asegurar que todo operario cuente con la competencia tcnica necesaria para el manejo humanitario y el cumplimiento de la normativa vigente.
        p.mb-0 Toda persona involucrada en el proceso debe realizar y aprobar el curso de capacitacin en bienestar animal, el cual constituye el principal mecanismo para estandarizar conocimientos y prcticas. Asimismo, el personal debe portar el documento que certifique la aprobacin del curso, permitiendo a las autoridades competentes verificar la idoneidad del operario.

