            h3.col.ps-3.mb-0 Obligaciones de cuidado y manejo (cargue y descargue)
        p.mb-0 La tcnica de manejo exige un comportamiento especfico por parte del operador con el fin de reducir el estrs y evitar el maltrato animal. El cargue y descargue deben realizarse con calma, sin ruidos ni hostigamiento, y sin el uso de fuerza innecesaria. El operador tiene la obligacin de utilizar las divisiones o barreras del vehculo para separar a los animales segn especie, edad, tamao y nivel de hostilidad.
