    #t_1_7.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.7 Normativa de seguridad y salud en el trabajo
    
    .row.justify-content-center.mb-5
      .col-lg-2.col-4.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema1/img54.svg' alt="Imagen decorativa")
      .col-lg-10(data-aos="fade-left")
        p La normativa especfica del transporte de animales en pie se enfoca principalmente en el bienestar animal, la bioseguridad y la seguridad vial. Por esta razn, no detalla de forma explcita los elementos de proteccin personal ni las tcnicas de higiene postural, las cuales se rigen por la legislacin general de seguridad y salud en el trabajo en Colombia.
        .cajon.color-secundario.p-4
          p.mb-0 La normativa de SST, reglamentada principalmente por el Decreto 1072 de 2015, establece la obligacin de los empleadores de suministrar y supervisar el uso de los elementos de proteccin personal, as como de implementar programas de higiene postural para mitigar los riesgos asociados a la actividad.

    .row.justify-content-center.mb-5
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Figura 1.
          i Beneficios de la normativa SST para el bienestar animal y la seguridad laboral en el transporte
        figure(data-aos="zoom-in")
          img.d-lg-flex.d-none(src='@/assets/curso/tema1/img55.svg' alt="Imagen decorativa")
          img.d-lg-none.d-flex(src='@/assets/curso/tema1/img56.svg' alt="Imagen decorativa")

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Elementos de proteccin personal (EPP)
    
    p Los elementos de proteccin personal constituyen la ltima barrera entre el riesgo y el trabajador. En el transporte de animales en pie, su uso es especialmente relevante durante las fases de cargue, descargue, inspeccin en ruta y mantenimiento del vehculo.
    p.mb-5 A continuacin, se presenta un recurso que relaciona las zonas del cuerpo a proteger, los EPP recomendados y su justificacin en el contexto del transporte animal.

    .titulo-sexto.color-acento-contenido(data-aos="fade-right")
      h5 Tabla 5.
      i Elementos de proteccin personal (EPP)
    .tabla-a.color-acento-contenido.mb-5
      table(data-aos="zoom-in"  alt="Texto alternativo tabla")
        thead(style="border-color: transparent")
          tr(data-aos="fade-right")
            th(style="background-color: #F3BC5B; width: 20%;") Zona para proteger
