        tbody
          tr(data-aos="fade-right")
            td(style="background-color: #F6F6F6;") Cabeza
            td(style="background-color: #F6F6F6;") Casco de seguridad
            td(style="background-color: #F6F6F6;") Proteccin contra impactos o cada de objetos durante las operaciones de cargue y descargue.
          tr(data-aos="fade-right")
            td Cuerpo
            td Chaleco reflectante o de alta visibilidad
            td Mejora la seguridad vial y la visibilidad durante inspecciones o paradas de emergencia.
          tr(data-aos="fade-right")
            td(style="background-color: #F6F6F6;") Ojos y rostro
            td(style="background-color: #F6F6F6;") Gafas de seguridad (transparentes o polarizadas)
            td(style="background-color: #F6F6F6;") Proteccin frente a polvo, partculas y salpicaduras de excretas o productos de limpieza.
          tr(data-aos="fade-right")
            td Manos
            td Guantes de proteccin (cuero, vaqueta o sinttico rugoso)
            td Prevencin de lesiones al manipular puertas, divisiones, rampas o herramientas.
          tr(data-aos="fade-right")
            td(style="background-color: #F6F6F6;") Pies
            td(style="background-color: #F6F6F6;") Calzado de seguridad (punta de acero o composite y suela antideslizante)
            td(style="background-color: #F6F6F6;") Proteccin contra golpes, aplastamientos y cadas en superficies hmedas o resbalosas.
          tr(data-aos="fade-right")
            td Vas respiratorias
            td Mascarilla o respirador (filtro N95 o superior)
            td Proteccin durante labores de limpieza, desinfeccin o exposicin a polvo ambiental.

