      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Relacin implcita entre la normativa y la SST
      
    p Aunque el manual no detalla EPP ni tcnicas posturales especficas, s establece requisitos estructurales y operativos que impactan directamente la seguridad del operario.
    p.mb-5 A continuacin, se presenta un recurso que resume esta relacin implcita.

    .tarjeta.BG08.p-4.mb-5(data-aos="fade-right")
      SlyderA.color-acento-botones(tipo="b" data-aos="zoom-in")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            .h4 Elementos de proteccin personal
            p #[strong Soporte documental (contexto del manual)]: el manual no detalla tipos de EPP (casco, botas de seguridad, guantes, etc.). La obligacin implcita recae en la necesidad de usar rampas seguras con pisos antideslizantes y la capacitacin, que debera incluir la seguridad laboral.
            p.mb-0 #[strong Nota]: el manual no detalla tipos de EPP especficos.
          .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema1/img57.jpg' alt="Imagen decorativa")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            .h4 Tcnicas de higiene postural
            p #[strong Soporte documental (contexto del manual)]: no se mencionan tcnicas de higiene postural. La obligacin es genrica a travs del Curso de Bienestar Animal, que debe incluir las acciones a tomar en caso de accidentes que involucren a los animales o al personal.
            p.mb-0 #[strong Nota]: no se describen tcnicas posturales especficas.
          .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema1/img58.jpg' alt="Imagen decorativa")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            .h4 Seguridad del operario
            p #[strong Soporte documental (contexto del manual)]: se requiere una rampa segura con mximo 20 grados de inclinacin, lo que indirectamente reduce el riesgo de lesiones y esfuerzo para el operario durante el manejo de la carga.
            p.mb-0 #[strong Nota]: reduce el riesgo de lesiones durante el manejo.
          .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema1/img59.jpg' alt="Imagen decorativa")

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tcnicas de higiene postural y ergonoma
    
    p El principal riesgo para el conductor es la fatiga y el desarrollo de lesiones osteomusculares asociadas a la postura prolongada y a las vibraciones del vehculo. La higiene postural se divide en dos fases: ajuste del puesto de conduccin y pausas activas durante la ruta.

    .BGIMG02.px-md-5.px-4.py-4.mb-4
      .row.justify-content-center
        .col-lg-10
          .mb-4.d-flex(data-aos="fade-right")
            .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
              img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
              h3.col.ps-3.mb-0 Tcnica de ajuste postural (en el asiento)

          p Antes de iniciar el viaje, el operario debe realizar los siguientes ajustes ergonmicos :

          ol.lista-ol--cuadro.lista-ol--separador.mb-0
            li(data-aos="fade-left") 
              .lista-ol--cuadro__vineta
                span 1
              p.mb-0 Ajuste del asiento (distancia): deslizar el asiento hasta mantener las rodillas ligeramente flexionadas y alcanzar los pedales sin esfuerzo.
            li(data-aos="fade-left") 
              .lista-ol--cuadro__vineta
                span 2
              p.mb-0 Ajuste del respaldo (ngulo): inclinar ligeramente el respaldo hasta un ngulo aproximado de 100 grados para apoyar la zona lumbar.
            li(data-aos="fade-left") 
              .lista-ol--cuadro__vineta
                span 3
              p.mb-0 Ajuste del reposacabezas: ubicarlo a la altura de las orejas o los ojos para prevenir lesiones cervicales.
            li(data-aos="fade-left") 
              .lista-ol--cuadro__vineta
                span 4
              p.mb-0 Ajuste del volante y brazos: colocar las manos en posicin tres y cuarto, con brazos ligeramente flexionados.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Tcnica de pausas activas (en ruta)
    
    p.mb-5 Las pausas activas complementan la obligacin legal de descanso y contribuyen a reducir la fatiga fsica.

    .row.justify-content-center.mb-4
      .col-lg-10
