    separador
    #t_1_8.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.8 Listas de chequeo y tiempos mximos de recorrido
    
    p.mb-5 Las listas de chequeo y el control de los tiempos mximos de recorrido son tcnicas de gestin operativa obligatorias orientadas a garantizar el bienestar animal y a prevenir accidentes asociados a la fatiga del personal durante el transporte de animales en pie. Estas tcnicas permiten verificar de manera peridica el estado de la carga viva, las condiciones estructurales del vehculo y el cumplimiento de los tiempos de conduccin establecidos por la normativa.

    .mb-4.d-flex(data-aos="fade-right")
