        h3.col.ps-3.mb-0 Contenido de las listas de chequeo (inspeccin en ruta)
    
    p.mb-5 Durante el recorrido, el conductor o tripulante debe realizar inspecciones peridicas de la unidad de transporte y de los animales movilizados. A continuacin, se presenta un recurso que organiza los principales puntos crticos que deben ser verificados en cada inspeccin, las cuales deben efectuarse cada cuatro (4) horas o durante las paradas programadas.

