    #t_1_9.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.9 Tipos de formatos y tcnicas de diligenciamiento de plan de ruta o plan de viaje
    
    p La documentacin del viaje y el diligenciamiento preciso de los registros operativos constituyen un mecanismo de control indispensable para verificar el cumplimiento de los tiempos de conduccin, las pausas de descanso y los protocolos de bienestar animal exigidos por las autoridades competentes (ICA y trnsito).
    p.mb-5 Aunque el manual no suministra un formato especfico denominado plan de ruta, s define los formatos y registros obligatorios que deben diligenciarse durante la movilizacin de animales en pie, los cuales se describen a continuacin.

    .row.justify-content-center.mb-5
      .col-auto(data-aos="fade-right")
        .tarjeta.BG07.p-3
          .h4.mb-0 Tipos de formatos y tcnicas de registro requeridos para el plan de viaje
    
    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="fade-right")
         LineaTiempoD.color-acento-botones.especial
          div(numero="1" titulo="Formato oficial de control")
            p Gua sanitaria de movilizacin (GSMI). Este es el formato de control sanitario indispensable para toda movilizacin legal de animales en pie. (Documento exigido por el ICA, implcito en el control oficial).
            figure
              img(src='@/assets/curso/tema1/img67.jpg' alt="Imagen decorativa")
          div(numero="2" titulo="Registro de tiempos")
            p La tcnica de diligenciamiento debe incluir el registro de las horas de conduccin y de descanso del conductor o tripulante, demostrando el cumplimiento de las ocho (8) horas de descanso tras ocho (8) horas de manejo.
            p #[strong Soporte documental]: manual de procedimiento (Num. 4.i): el conductor o tripulante deber descansar al menos ocho (8) horas .
            figure
              img(src='@/assets/curso/tema1/img68.jpg' alt="Imagen decorativa")
          div(numero="3" titulo="Registro de novedades")
            p El plan de viaje debe registrar las interrupciones y las actividades realizadas en el lugar de descanso, tales como inspeccin, alojamiento, hidratacin o alimentacin.
            p #[strong Soporte documental]: manual de procedimiento (Num. 4.h): menciona las interrupciones para realizar actividades.
            figure
              img(src='@/assets/curso/tema1/img69.jpg' alt="Imagen decorativa")
          div(numero="4" titulo="Registro de inspecciones")
            p La tcnica exige el registro de las inspecciones realizadas cada cuatro (4) horas, detallando el estado de la unidad de transporte y de los animales en movimiento.
            p #[strong Soporte documental]: manual de procedimiento (Num. 4.g): inspeccin de la unidad de transporte por lo menos cada cuatro (4) horas .
            figure
              img(src='@/assets/curso/tema1/img70.jpg' alt="Imagen decorativa")

    .row.justify-content-center.mb-4
      .col-lg-3.col-6.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema1/img71.svg' alt="Imagen decorativa")
      .col-lg-9(data-aos="fade-left") 
        .mb-4.d-flex
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Tcnicas de diligenciamiento del plan de ruta (registros operacionales)
        p.mb-5 El plan de ruta se diligencia mediante el registro continuo y ordenado de las acciones operativas y de bienestar animal realizadas durante el viaje, conforme a los tiempos mximos y mnimos establecidos por la normativa vigente.
        .mb-4.d-flex
          .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
            img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
            h3.col.ps-3.mb-0 Tcnica de registro de tiempos de conduccin y descanso
        p.mb-0 El diligenciamiento debe evidenciar el cumplimiento estricto de los tiempos de conduccin y de descanso obligatorios, con el fin de prevenir la fatiga del personal y reducir el riesgo de accidentes durante el transporte.

    separador
    #t_1_10.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.10 Lugares de sombra y #[i stress] trmico
    
    p.mb-5 La provisin de sombra es una tcnica de proteccin contra las condiciones climticas adversas, principalmente frente a la radiacin solar directa, y constituye un elemento clave para la prevencin del estrs trmico durante el transporte animal en pie.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Mitigacin estructural (sombra fija)
    
    .row.justify-content-center.mb-5
      .col-lg-4.col-sm-6.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema1/img72.jpg' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        p La tcnica principal para proveer sombra se basa en las caractersticas estructurales de la unidad de transporte, las cuales son de cumplimiento obligatorio segn la normativa vigente.
        .row.justify-content-center
          .col-lg-6.mb-lg-0.mb-4
            .tarjeta-avatar
              img(src='@/assets/curso/tema1/img73.svg' alt="Imagen decorativa")
              .tarjeta.BG03
                .p-4
                  .h4.text-center Techo o cubierta
                  p.mb-0 El vehculo seleccionado debe contar con techo o cubierta que proteja a los animales de las condiciones climticas adversas, lo que incluye la exposicin directa al sol como principal fuente de estrs trmico.
          .col-lg-6.mb-lg-0.mb-4
            .tarjeta-avatar
              img(src='@/assets/curso/tema1/img74.svg' alt="Imagen decorativa")
              .tarjeta.BG03
                .p-4
                  .h4.text-center Soporte documental
                  p.mb-0 "Las unidades de transporte debern contar con techo o cubierta que proteja a los animales de las condiciones climticas adversas." Resolucin 20223040006915 (nm. 2.2.ci).

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Mitigacin operacional (lugares de descanso)
    
    p.mb-5 El plan de viaje debe contemplar interrupciones operativas que permitan la bsqueda y utilizacin de lugares adecuados que proporcionen sombra o refugio cuando las condiciones ambientales lo requieran.

    .row.justify-content-center.mb-5
      .col-lg-10
        .tarjeta--container.row
          .col-md.tarjeta.color-primario.p-5(data-aos="fade-right")
            .row.justify-content-center.mb-4
              .col-6
                figure
                  img(src='@/assets/curso/tema1/img75.svg', alt="Imagen decorativa")
            h4.text-center Uso de lugares de descanso
            p.mb-0 En el orden de viaje, las interrupciones prolongadas deben realizarse en lugares de descanso de los animales que ofrezcan proteccin adecuada frente a las condiciones climticas.
          .col-md.tarjeta.color-secundario.p-5(data-aos="fade-left")
            .row.justify-content-center.mb-4
              .col-6
                figure
                  img(src='@/assets/curso/tema1/img76.svg', alt="Imagen decorativa")
            h4.text-center Propsito
            p.mb-0 Durante estas pausas, la tcnica de cuidado exige realizar actividades de inspeccin, acomodacin, hidratacin o alimentacin, siendo la provisin de sombra un componente implcito de la acomodacin para mitigar el riesgo de golpe de calor.

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="fade-right")
        .cajon.color-secundario.p-4
          .row.justify-content-center
            .col-lg-3.col-6.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img77.svg' alt="Imagen decorativa")
            .col-lg-9
              .h4 Soporte documental
              p.mb-0 "En los casos de interrupciones, estas se deben realizar en un lugar de descanso de los animales para realizar actividades de inspeccin, acomodacin, hidratacin o alimentacin." Manual de procedimiento (nm. 4.h).

    p El estrs trmico, entendido como el exceso de calor o fro, es uno de los principales factores de riesgo que afectan la salud y el bienestar animal durante el transporte. La normativa establece tcnicas estructurales y operativas especficas para mitigar este riesgo, siendo la provisin de sombra y el control de la ventilacin los elementos centrales.
    p.mb-5 A continuacin, se presentan los criterios tcnicos que regulan el control del estrs trmico en el transporte animal en pie.

    .titulo-sexto.color-acento-contenido(data-aos="fade-right")
      h5 Tabla 7.
      i Control del estrs trmico
    .tabla-a.color-acento-contenido.mb-4
      table(data-aos="zoom-in"  alt="Texto alternativo tabla")
        thead(style="border-color: transparent")
          tr(data-aos="fade-right")
            th(style="background-color: #F3BC5B; width: 24%;") Tcnica de control de estrs trmico
            th(style="background-color: #F3BC5B; width: 38%;") Requisito detallado
            th(style="background-color: #F3BC5B; width: 38%;") Soporte documental (texto exacto)
        tbody
          tr(data-aos="fade-right")
            th(style="background-color: #F6F6F6;") Control de la temperatura
            td(style="background-color: #F6F6F6;") La tcnica de manejo debe procurar que la temperatura dentro del vehculo no sea inferior a 5 C ni superior a 30 C.
            td(style="background-color: #F6F6F6;") "...asegurar la temperatura que no podr ser inferior a 5 C ni superior a 30 C." Manual de procedimiento (nm. 4.f).
          tr(data-aos="fade-right")
            th Ventilacin obligatoria
            td Para mantener el rango de temperatura, el vehculo debe garantizar la ventilacin constante en todos sus compartimentos, evitando la acumulacin de calor (hipertermia) o la falta de oxgeno.
            td "Procurar mantener la ventilacin constante." Manual de procedimiento (nm. 4.f).
          tr(data-aos="fade-right")
            th(style="background-color: #F6F6F6;") Diseo vehicular (porcinos)
            td(style="background-color: #F6F6F6;") En los vehculos con ventilacin natural, la altura libre mnima por compartimento debe ser de 90 cm para permitir un flujo de aire adecuado en especies sensibles como los porcinos.
            td(style="background-color: #F6F6F6;") Mencionado en la norma como requisito para mitigar el riesgo de estrs trmico en especies especficas.

    p El manejo del estrs trmico y la provisin de sombra constituyen tcnicas de cuidado ambiental esenciales para garantizar el bienestar animal durante el transporte.

    separador
    #t_1_11.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.11 Planes de contingencia
    
    p Los planes de contingencia y emergencia son un componente obligatorio del proceso de transporte, manejo y movilizacin de animales en pie. Su objetivo es establecer los procedimientos y las acciones que deben ejecutar los operadores (propietario, conductor y tripulante) para atender y mitigar el sufrimiento, el dolor y la mortalidad de los animales que resulten enfermos, lesionados o muertos durante el cargue, la ruta o la descarga, as como en caso de accidente.
    p.mb-5 Para garantizar una respuesta adecuada ante situaciones crticas, la normativa define los siguientes aspectos mnimos que deben estar cubiertos en el plan de contingencia:

    TabsA.color-acento-botones.mb-5(data-aos="fade-right")
      .tarjeta.BG10.p-4(titulo="Eventos de contingencia")
        p El plan debe cubrir situaciones crticas como animales enfermos, muertos o accidentes que resulten en animales atrapados, cadas, traumatismos o fracturas.
        p.mb-0 #[strong Soporte documental]: ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (pg. 6): Ante animales enfermos, muertos o accidentes que conlleven animales atrapados, cadas, traumatismos o fracturas de estos.
      .tarjeta.BG10.p-4(titulo="Acciones inmediatas")
        p El plan debe incluir acciones inmediatas, como la ayuda o atencin inmediata al animal y la notificacin a la autoridad sanitaria ms cercana.
        p.mb-0 #[strong Soporte documental]: ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (pg. 6): las van desde la ayuda o atencin inmediata al animal, informar a la Oficina del Instituto Colombiano Agropecuario ICA ms cercana.
      .tarjeta.BG10.p-4(titulo="Disposicin sanitaria")
        p Un componente crtico del plan es la tcnica de sacrificio y disposicin adecuada de los animales muertos en ruta, para evitar riesgos biolgicos y ambientales.
        p.mb-0 #[strong Soporte documental]: ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (pg. 6): sacrificio y disposiciones de los animales muertos.
      .tarjeta.BG10.p-4(titulo="Inclusin en capacitacin")
        p Las acciones del plan de contingencia deben ser un componente obligatorio del curso de capacitacin en bienestar animal.
        p.mb-0 #[strong Soporte documental]: Manual de procedimiento (nm. 5): Las acciones a tomar por parte del conductor o tripulante frente a las situaciones anteriores debern estar incluidas dentro del plan de estudios del curso de capacitacin segn la especie.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.BG04.pe-4(style="border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Obligacin y tipos de eventos de contingencia
    

    .row.justify-content-center.mb-5
      .col-lg-1.col-2.mb-lg-0.mb-4
        img(src='@/assets/curso/temas/img01.svg' alt="Imagen decorativa")
      .col-lg-11.col-10
        p La existencia, conocimiento y aplicacin del plan de contingencia es un requisito obligatorio para todos los actores involucrados en la cadena de transporte de animales en pie.
        p.mb-0 En relacin con esta obligacin, la normativa establece los siguientes lineamientos:

    .tarjeta.BG10.p-4.mb-4
      SlyderA.color-acento-botones(tipo="b" data-aos="zoom-in")
        .row.justify-content-center.align-items-center
          .col-lg-8.order-lg-1.order-2
            .h4 Obligatoriedad
            p El plan debe ser conocido y aplicado por el propietario, poseedor, tenedor, conductor, transportador y/o tripulante.
            p.mb-0 #[strong Soporte documental]: ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (1).pdf, pg. 6: El propietario, poseedor, tenedor, conductor, transportador y/o tripulante deber tener en cuenta las instrucciones contenidas en el manual, para atender la situacin de emergencia que se presenta.
          .col-lg-4.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema1/img78.jpg' alt="Imagen decorativa")
        .row.justify-content-center.align-items-center
          .col-lg-8.order-lg-1.order-2
            .h4 Eventos por cubrir
            p El plan debe estar diseado para responder ante animales enfermos, muertos o accidentes que conlleven animales atrapados, cadas, traumatismos o fracturas, durante el proceso de cargue, transporte y descarga.
            p.mb-0 #[strong Soporte documental]: ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (1).pdf, pg. 6: Ante animales enfermos, muertos o accidentes que conlleven animales atrapados, cadas, traumatismos o fracturas de estos, durante el proceso de cargue, transporte y descarga de los animales.
          .col-lg-4.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema1/img79.jpg' alt="Imagen decorativa")
        .row.justify-content-center.align-items-center
          .col-lg-8.order-lg-1.order-2
